.shake{
  transition : 500ms;
}
.shake:hover {  
  -webkit-animation: shake 0.5s;  
          animation: shake 0.5s; /* Start the shake animation and make the animation last for x seconds  */
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; /* When the animation is finished, start again */ 
}

@-webkit-keyframes shake{
  50%{-webkit-transform:translate(0, -0.5vw) rotate(0deg);transform:translate(0, -0.5vw) rotate(0deg)}
  0%,100%{-webkit-transform:translate(0, 0) rotate(0);transform:translate(0, 0) rotate(0)}
}

@keyframes shake{
  50%{-webkit-transform:translate(0, -0.5vw) rotate(0deg);transform:translate(0, -0.5vw) rotate(0deg)}
  0%,100%{-webkit-transform:translate(0, 0) rotate(0);transform:translate(0, 0) rotate(0)}
}


.appearing {
  -webkit-animation:  appearing 1s;
          animation:  appearing 1s;
  visibility: visible !important;
}

@-webkit-keyframes appearing{
  0%{opacity: 0;}
  100%{opacity: 1;}
}

@keyframes appearing{
  0%{opacity: 0;}
  100%{opacity: 1;}
}

.disappearing{
  -webkit-animation:  disappearing 1s;
          animation:  disappearing 1s; 
  -webkit-animation-fill-mode:forwards; 
          animation-fill-mode:forwards; /* pour que l'element reste à l'état final */
  visibility: visible !important;
}

@-webkit-keyframes disappearing{
  0%{opacity: 1;}
  100%{opacity: 0;visibility: hidden;} 
}

@keyframes disappearing{
  0%{opacity: 1;}
  100%{opacity: 0;visibility: hidden;} 
}

