  @-webkit-keyframes top_anim{
    0.001%{opacity: 1;line-height: 5vw;-webkit-transform: translate(0px,0px);transform: translate(0px,0px);}
    /* pour corriger l'incompréhensible décalage d'1 pixel entre le haut et le bas */
    10%{opacity:  1;line-height: 2.5vw;}
    75%{opacity: 1}
    0%,100%{opacity: 0;-webkit-transform: translate(0px,0px);transform: translate(0px,0px);}
    /* pour corriger l'incompréhensible décalage d'1 pixel entre le haut et le bas */
  }
  @keyframes top_anim{
    0.001%{opacity: 1;line-height: 5vw;-webkit-transform: translate(0px,0px);transform: translate(0px,0px);}
    /* pour corriger l'incompréhensible décalage d'1 pixel entre le haut et le bas */
    10%{opacity:  1;line-height: 2.5vw;}
    75%{opacity: 1}
    0%,100%{opacity: 0;-webkit-transform: translate(0px,0px);transform: translate(0px,0px);}
    /* pour corriger l'incompréhensible décalage d'1 pixel entre le haut et le bas */
  }

  @-webkit-keyframes bot_anim{
    0.001%{opacity: 1;-webkit-transform: translate(0px,-1.25vw);transform: translate(0px,-1.25vw);}
    10%{opacity:  1;-webkit-transform: translate(0px,0px);transform: translate(0px,0px);}
    75%{opacity: 1}
    0%,100%{opacity: 0}
  }

  @keyframes bot_anim{
    0.001%{opacity: 1;-webkit-transform: translate(0px,-1.25vw);transform: translate(0px,-1.25vw);}
    10%{opacity:  1;-webkit-transform: translate(0px,0px);transform: translate(0px,0px);}
