.wrapper-form {
  position : relative; 
  display: grid; /* ceci est une grille */
  grid-template-columns: 1fr; /* 1 colonne qui occupe tout l'espace disponible */
  justify-items: center; /* les enfants sont centrés verticalement */
  grid-auto-flow: column; /* les enfants sont placés au fur et à mesure dans la colonne */
  grid-auto-rows: auto;
}

.box-form{ /* flexbox */
  display: flex;
  flex-direction: column;
  grid-column: 1;
  grid-row: auto;
  background-color: rgba(33,33,33,1);
  border: 1px solid rgb(50,50,50);
  border-radius: 20px;
}

.item-form{ /* flexbox's childs */
